.iconmoon {
  display: block;
  text-align: center;
  height: 28px;
  @include iconmoon();
  &:before {
    font-size: 24px;
  }
}
.icons {
  &-wrap {
    margin-right: 6px;
    cursor: pointer;
    position: relative;
  }
}
.icons {
  &__name {
    display: block;
    font-size: .6875rem;
    color: $white-color;
    font-family: $primary-font-light !important;
    text-transform: lowercase;
  }
}